.recommendations {
    background-color: #004047;
    // height: 300px;
    text-align: center;
    padding: 140px 165px;

    .title {
        font-family: 'livvic-bold';
        font-style: normal;
        font-weight: 700;
        font-size: 32px;
        line-height: 38px;
        color: white;
        width: 932px;
        margin: auto;
        margin-bottom: 86px;
        letter-spacing: -0.05px;

        span {
            color: #79c8c7;
            font-style: normal;
            letter-spacing: normal;
            line-height: normal;
        }
    }

    .recommendations-section {
        display: flex;
        gap: 30px;
        width: fit-content;
        margin: auto;

        .recommendation {
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;

            .icon-quotes {
                position: absolute;
                height: 56px;
                width: auto;
                top: -36px;
            }

            .body {
                color: white;
                font-family: 'livvic-semibold';
                font-family: 'livvic';
                font-weight: 600;
                line-height: 25px;
                font-size: 15px;
                width: 350px;
                margin: auto;
                margin-bottom: 24px;
                z-index: 10;
                letter-spacing: 0.3px;
            }

            .name {
                color: #79C8C7;
                font-family: 'livvic-bold';
                font-style: normal;
                font-weight: 700;
                font-size: 18px;
                line-height: 28px;
                margin-bottom: 2px;
                font-weight: 700;
            }

            .job-description {
                font-family: 'livvic-medium-italic';
                font-size: 13px;
                color: white;
                margin-bottom: 43px;
            }

            .img-container {
                border: 2px solid #c4fffe;
                border-radius: 50%;
                overflow: hidden;
                height: 66px;
                width: 66px;

                img {
                    width: 100%;
                }
            }
        }
    }
}

//MEDIA QUERY 

@media (max-width: 440px) {
    .recommendations {
        padding: 140px 24px;

        .title {
            font-size: 22px;
            width: auto;
            line-height: 42px;

            span {
                line-height: 42px;
            }
        }

        .recommendations-section {
            flex-direction: column;
            gap: 84px;

            .recommendation {
                .body {
                    width: fit-content;
                }
            }
        }
    }
}