.hero {
    min-height: 700px;
    background-color: #014e56;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;

    .body {
        margin: auto;
        display: flex;
        gap: 30px;
        align-items: flex-end;
        justify-content: center;
        height: 201px;

        .title {
            width: 635px;
            font-family: "livvic-bold";
            color: white;
            font-size: 88px;
            line-height: 96px;

            span {
                color: #f67e7e;
            }
        }

        .description {
            width: 445px;
            font-size: 18px;
            color: white;
            font-family: "livvic-semibold";
            font-weight: 600;
            font-style: normal;
            letter-spacing: normal;
            line-height: 28px;
        }
    }

    .pattern-hero {
        position: absolute;
        transform: translate(50%,50%);
        bottom: 0;
        right: 30%;
        width: 358px;
    }
}

//MEDIA QUERY 

@media (max-width: 440px) {
    .hero {
        padding: 0 24px;
        min-height: 567px;

        .body {
            flex-direction: column;
            width: 100%;
            text-align: center;

            .title {
                font-size: 40px;
                width: 100%;
                line-height: 50px;
                padding: 0 10%;
            }

            .description {
                width: 100%;
                font-size: 16px;
            }
        }
    }
}