.introduction {
    height: 700px;
    background-color: #012f34;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;

    .main {
        display: flex;
        gap: 123px;

        .title {
            width: 445px;
            color: white;
            font-family: "livvic-bold";
            font-size: 32px;
            line-height: 48px;
            margin-top: 8px;
        }

        .things-we-can-do {
            display: flex;
            flex-direction: column;
            gap: 35px;

            .item {
                display: flex;
                gap: 23px;

                .description {
                    width: 445px;

                    h3 {
                        color: #f67e7e;
                        font-size: 18px;
                        font-family: "livvic-bold";
                        margin-bottom: 16px;
                    }

                    p {
                        color: white;
                        font-family: "livvic-semibold";
                        font-size: 14px;
                        line-height: 25px;
                    }
                }

                .img-container {
                    width: 65px;
                }
            }
        }
    }

    .pattern-introduction {
        position: absolute;
        right: 0;
        bottom: 0;
        transform: translate(50%, 0);
        height: 244px;
    }
}

@media (max-width: 440px) {
    .introduction {
        padding: 70px 24px;
        padding-bottom: 100px;
        min-height: 567px;
        height: auto;

        .main {
            flex-direction: column;
            width: 100%;
            text-align: center;
            gap: 55px;

            .title {
                font-size: 24px;
                align-self: flex-start;
                text-align: left;
                width: 240px;
            }

            .things-we-can-do {
                .item {
                    flex-direction: column;
                    align-items: center;

                    .description {
                        width: 100%;
                    }
                }
            }
        }
    }
}