.contact {
    height: 200px;
    background-color: #f67e7e;
    position: relative;
    overflow: hidden;

    .main-layout {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        max-width: 917px;

        h2 {
            color: #012f34;
            font-family: "livvic-bold";
            font-size: 48px;
            line-height: 48px;
        }
    }

    .pattern-contact {
        position: absolute;
        bottom: 20px;
        transform: translate(0, 50%);
        width: 200px;
        height: 244px;
    }
}