.app-header {
    display: flex;
    justify-content: center;
    position: absolute;
    top: 0;
    width: 100%;
    margin-top: 77px;
    z-index: 10;

    .main {
        display: flex;
        max-width: 1100px;
        justify-content: space-between;
        align-items: center;
        margin: auto;
        width: 100%;

        .left {
            display: flex;
            align-items: center;
            gap: 80px;

            .logo {
                display: flex;
                align-items: center;
                font-family: "livvic-bold";
                color: white;
                font-size: 24px;
            }

            .nav {
                display: flex;
                align-items: center;
                gap: 40px;
                color: white;
                font-family: "livvic-semibold";
                font-size: 18px;
            }
        }

        .right {
            height: 100%;
            display: flex;
            align-items: center;

            .contact-btn {
                height: 100%;
                display: flex;
                align-items: center;

            }
        }
    }
}

//MEDIA QUERY 

@media (max-width: 440px) {
    .app-header {
        margin-top: 48px;
        padding: 0 24px;

        .main {
            .left {
                .nav {
                    display: none;
                }
            }

            .right {
                display: none;
            }
        }
    }
}