.main-layout {
    max-width: 1110px;
    margin: auto;
}

.btn {
    padding: 8px 24px;
    font-size: 18px;
    font-family: "livvic-semibold";
    line-height: 28px;
    color: white;
    background-color: transparent;
    border: 2px solid #ffffff;
    border-radius: 24px;

    &.dark {
        color: #012f34;
        border: 2px solid #012f34;
    }
}